html {
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
}

body {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  padding: 0px;
  margin: 0px;
  /* background-image: linear-gradient(rgb(255, 179, 179), rgb(187, 191, 255)); */
  color: black;
}

nav {
  background-color: black;
  padding: 0px 0px;

  position: fixed;
  width: 100%;
  z-index: 1;
}

.nav-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
}

.nav-title {
  width: 100%;
  display: grid;
  place-items: start;
  margin-left: 20px;
}

h1 {
  color: white;
  padding: 20px 0px 20px 0px;
  margin: 0px;
}

a {
  margin: 0px;
  padding: 0px;
  width: 100%;
  display: grid;
  place-items: center;
  color: white;
  text-decoration: none;
}

a div:active {
  background-color: rgb(31, 31, 31);
}

.cart-icon {
  position: relative;
  float: right;
  color: white;
  font-size: 1.5rem;
  display: grid;
  place-items: center end;
  grid-template-columns: 1fr;
  width: 100%;
  margin: auto;
}

.cart-count {
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  margin-right: 20px;
}

.cart {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 100px auto;

  width: 90%;
}

.customer-form button {
  width: 300px;
  margin-top: 20px;
}

.stripe-button button {
  width: 300px;
}

.cart table {
  margin: 0px auto;
}

.prints {
  padding-top: 100px;

  margin: auto;
  place-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 50px 0px;
  animation-name: fadeIn;
  opacity: 0;
  animation-delay: 0s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.print {
  width: 300px;
}

.print-box {
  height: 450px;
  display: grid;
  place-items: center;
}

.price-width {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.print img {
  width: 300px;
  height: 450px;

  animation-name: fadeIn;
  opacity: 0;

  animation-duration: 1s;
  animation-fill-mode: forwards;
}

button {
  border: 0px solid black;
  border-radius: 25px;
  background-color: black;
  color: white;
  font-family: "Montserrat", sans-serif;

  padding: 10px;
  transition: 0.3s;
  cursor: pointer;
  margin: 5px;
  font-size: 0.8rem;
}

button:hover {
  background-image: none;
  background-image: linear-gradient(
    to right,
    rgb(255, 96, 96),
    rgb(0, 17, 255)
  );
  border-color: black;
}

button:disabled {
  background-image: linear-gradient(
    to right,
    rgb(255, 96, 96),
    rgb(0, 17, 255)
  );
}

table {
  width: 400px;
  max-width: 100vw;
}

tr {
  height: 50px;
}

.loading {
  height: 100vh;
  display: grid;
  place-items: center;
  position: relative;
  margin: auto;
}

.customer-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(auto, 1fr);
  place-items: center;
}

.customer-form div {
  display: grid;
  grid-template-rows: repeat(auto, 1fr);
  text-align: left;
  width: 300px;
}

.customer-form label {
  font-weight: 800;
}

@media screen and (max-width: 900px) {
  .prints {
    padding-top: 100px;
    margin: auto;
    place-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 50px 0px;
    animation-name: fadeIn;
    opacity: 0;
    animation-delay: 0s;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }
  .cart {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

@media screen and (max-width: 600px) {
  .nav-div {
    margin: auto;
    float: none;
    width: 100%;
  }
  .prints {
    padding-top: 100px;

    margin: auto;
    place-items: center;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    gap: 50px 0px;
  }
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 1rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(0px, -30px);
  }
  to {
    opacity: 1;
    transform: translate(0px, 0px);
  }
}
